/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React from 'react';

const ShippingAndBilling = (props) => {
    return (
        <>
            <div className='row'>
                <div className="col-md-8">
                    <div className='row'>
                         <div className="col-md-12">
                            <span className='title-tab broder-bottom'>Shipping</span>
                         </div>   
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">First Name</label>
                                <div className="col-md-7">
                                    <input type="text" 
                                    name="firstName" 
                                    value={props.Data.customerShipTo.firstName}
                                    onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                    className="form-control" placeholder="First Name"></input>
                                </div>
                            </div>
                        </div>
                       
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Company Name</label>
                                <div className="col-md-7">
                                    <input type="text" 
                                    name="companyName"
                                    value={props.Data.customerShipTo.companyName}
                                    onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     className="form-control" placeholder="Company Name"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Last Name</label>
                                <div className="col-md-7">
                                    <input type="text" 
                                    name="lastName"
                                    value={props.Data.customerShipTo.lastName}
                                    onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     className="form-control" placeholder="Last Name"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Phone Number</label>
                                <div className="col-md-7">
                                    <input
                                     type="text"
                                     name="phone"
                                     value={props.Data.customerShipTo.phone}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     className="form-control" placeholder="Phone Number"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Street Address</label>
                                <div className="col-md-7">
                                    <input 
                                    type="text"
                                    name="address1"
                                    value={props.Data.customerShipTo.address1}
                                    onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                      className="form-control" placeholder="Street Address"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">City</label>
                                <div className="col-md-7">
                                    <input type="text"
                                    name="city"
                                     value={props.Data.customerShipTo.city}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                      className="form-control" placeholder="City"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Extended Address</label>
                                <div className="col-md-7">
                                    <input type="text" 
                                     name="address2"
                                     value={props.Data.customerShipTo.address2}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                    className="form-control" placeholder="Extended Address"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">State</label>
                                <div className="col-md-7">
                                    <input type="text"
                                    name="state"
                                     value={props.Data.customerShipTo.state}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                      className="form-control" placeholder="State"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Zip Code</label>
                                <div className="col-md-7">
                                    <input type="text"
                                    name="zipCode" 
                                     value={props.Data.customerShipTo.zipCode}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     className="form-control" placeholder="Zip Code"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Country</label>
                                <div className="col-md-7">
                                    <input type="text"
                                    name="country"
                                     value={props.Data.customerShipTo.country}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                      className="form-control" placeholder="Country"></input>
                                </div>
                            </div>
                        </div>
                    </div>


                </div>
                <div className="col-md-4">
                    <div className='row'>
                    <div className="col-md-12">
                        <span className='title-tab broder-bottom'>Billing</span>
                    </div>    
                        <div className="col-md-12">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Bill To</label>
                                <div className="col-md-7">
                                    <input type="text"
                                     value={props.Data.customerShipTo.attn}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     name="Company Name" className="form-control" placeholder="Bill To"></input>
                                </div>
                            </div>
                        </div>

                        <div className="col-md-12">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Primary Contact</label>
                                <div className="col-md-7">
                                    <input type="text" 
                                    name="billContact1"
                                     value={props.Data.customerShipTo.billContact1}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     className="form-control" placeholder="Primary Contact"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Secondary Contact</label>
                                <div className="col-md-7">
                                    <input type="text" 
                                    name="billContact2"
                                     value={props.Data.customerShipTo.billContact2}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     className="form-control" placeholder="Secondary Contact"></input>
                                </div>
                            </div>
                        </div>

                        <div className="col-md-12">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Terms</label>
                                <div className="col-md-7">
                                    <input type="text"
                                    name="term"
                                     value={props.Data.customerShipTo.term}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                      className="form-control" placeholder="Terms"></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Resale Number</label>
                                <div className="col-md-7">
                                    <input type="text"
                                     name="resaleNum"
                                     value={props.Data.customerShipTo.resaleNum}
                                     onChange={(e) => { props.handleChange(e.target.name,e.target.value); }}
                                     className="form-control" placeholder="Resale Number"></input>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default ShippingAndBilling;