/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React from 'react';
import Select from 'react-select';
import TextField from '@mui/material/TextField';
import { CusDateFormater } from 'Helpers/StringHelpers';
import { TagsInput } from 'Components/Utility';
import CurrencyInput from 'react-currency-input-field';

const GeneralInfo = (props) => {

    return (
        <>
            <div className="price-levels-top">
                <div className="icons-list price-levels-btn-right">
                    <button className="levels-plus-icons icons-hd-cm">PM USA LOYALTY</button>
                </div>
            </div>
            <div className="row">
                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">Email Id</label>
                        <div className="col-md-5">
                            <input type="text"
                                defaultValue={props.Data.email}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                name="email"
                                className="form-control"
                                placeholder="abc@example.com"></input>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">Company Name</label>
                        <div className="col-md-5">
                            <input type="text" name="companyName"
                                defaultValue={props.Data.companyName}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="Company Name"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-6 col-form-label">Primary Phone</label>
                        <div className="col-md-6">
                            <input type="text" name="phone1"
                                defaultValue={props.Data.phone1}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="Primary Phone"></input>
                        </div>
                    </div>
                </div>



                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">Street Address</label>
                        <div className="col-md-5">
                            <input type="text"
                                name="address1"
                                defaultValue={props.Data.address1}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="Street Address"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">Alternate Phone</label>
                        <div className="col-md-5">
                            <input type="text"
                                name="phone2"
                                defaultValue={props.Data.phone2}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="Alternate Phone"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-6 col-form-label">Birthday</label>
                        <div className="col-md-6">

                            <TextField
                                className='data-picker-field Expiration-Coupon'
                                defaultValue={props.Data.dob !== null ? CusDateFormater(props.Data.dob) : props.Data.dob}
                                id="datetime-local"
                                placeholder='Birthday'
                                type="date"
                                name="dob"
                                sx={{
                                    '& legend': { display: 'none' },
                                    '& fieldset': { top: 0 },
                                }}
                                onChange={(e) => {
                                    props.handleChange(e.target.name, e.target.value);
                                }}
                            />
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">Street Address2</label>
                        <div className="col-md-5">
                            <input type="text"
                                name="address2"
                                defaultValue={props.Data.address2}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="Street Address2"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">City</label>
                        <div className="col-md-5">
                            <input type="text"
                                name="city"
                                defaultValue={props.Data.city}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="City"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4 d-none">
                    <div className="form-group row tab-group">
                        <label className="col-sm-6 col-form-label">Application Date</label>
                        <div className="col-md-6">
                            <TextField
                                className='data-picker-field Expiration-Coupon'
                                defaultValue={props.Data.dob !== null ? CusDateFormater(props.Data.dob) : props.Data.dob}
                                id="datetime-local"
                                name="applicationDate"
                                type="date"
                                sx={{
                                    '& legend': { display: 'none' },
                                    '& fieldset': { top: 0 },
                                }}
                                onChange={(e) => {
                                    props.handleChange(e.target.name, e.target.value);
                                }}
                            />

                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-6 col-form-label">State</label>
                        <div className="col-md-6">
                            <input type="text"
                                name="state"
                                defaultValue={props.Data.state}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="State"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">Zip Code</label>
                        <div className="col-md-5">
                            <input type="text"
                                name="zipCode"
                                defaultValue={props.Data.zipCode}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="Zip Code"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">County</label>
                        <div className="col-md-5">
                            <input type="text"
                                name="county"
                                defaultValue={props.Data.county}
                                onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                className="form-control" placeholder="County"></input>
                        </div>
                    </div>
                </div>

                <div className="col-md-4">
                    <div className="form-group row tab-group">
                        <label className="col-sm-6 col-form-label">Discount Pecentage</label>
                        <div className="col-md-6">
                            <CurrencyInput className='form-control'
                                suffix='%'
                                defaultValue={props.Data.discountPercentage}
                                onValueChange={(e) => {
                                    props.handleChange('discountPercentage', e);
                                }}
                            />

                        </div>
                    </div>
                </div>

                <div className="col-md-4 d-none">
                    <div className="form-group row tab-group">
                        <label className="col-sm-4 col-form-label">Price Level</label>
                        <div className="col-md-5">
                            <Select options={[]} className='product-depatment'
                                menuPortalTarget={document.body} menuPosition={'fixed'}
                            />
                        </div>
                    </div>
                </div>

                <div className="col-md-4 d-none">
                    <div className="form-group row tab-group">
                        <label className="col-sm-6 col-form-label">Bonus Ponints Achieved</label>
                        <div className="col-md-6">
                            <CurrencyInput
                                className='form-control'
                                name="bonusPointAchived"
                                id="qty"
                                data-number-to-fixed="5"
                                data-number-stepfactor="100"
                                defaultValue={props.Data.bonusPointAchived}
                                placeholder=""
                                onValueChange={(e) => {
                                    props.handleChange('bonusPointAchived', e);
                                }}
                                allowDecimals
                                decimalsLimit="5"
                                disableAbbreviations
                            //disabled={MixAndMatchData.priceGroupTypeId !== 1}
                            />

                        </div>
                    </div>
                </div>

                <div className="col-md-4 department-row">
                    <div className="form-group row tab-group">
                        <label className="col-sm-3 col-form-label">Card Swipe IDs</label>
                        <div className="col-md-8 card-swipe-bx">
                            <TagsInput selectedTags={(e) => {
                                props.handleChange('swipeIds', e);
                            }}
                                tags={props.Data.swipeIds} />
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="info-bx">
                        <div className="form-check">
                            <label className="form-check-label">Tax Exempt</label>

                            <input className='form-check-input'
                                name='inactive'
                                checked={props.Data.inactive}
                                type='checkbox'
                                value='' />
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default GeneralInfo;