/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import { useLocation, useNavigate } from 'react-router-dom';
import { ProductInitState } from 'Model/Product/Product.model';
import { Customerinit } from 'Model/Customer.model';
import * as CustomerAPI from 'Services/Customer';
import AccountInfo from './AccountInfo';
import GeneralInfo from './GeneralInfo';
import History from './History';
import ShippingAndBilling from './ShippingAndBilling';
import queryString from 'query-string';
import swal from 'sweetalert2';
import DefaultConfig from 'Config/GlobalVariable';
import {CustomerSchema} from 'Validation';


const Customer = () => {
    //for validation
    
    const navigate = useNavigate();
    const QueryParams = queryString.parse(useLocation().search);
    // const [ProductInfo, SetProductInfo] = useState(ProductInitState);
    const [CustomerInfo, SetCustomerInfo] = useState(Customerinit);
    const [value, setValue] = useState(0);
    const [EditMode, setEditMode] = useState(0);
    const handletabChange = (event, newValue) => setValue(newValue);

    const LoadCustomer = (Id) => {
                setEditMode(true);
                CustomerAPI.CustomerById(Id).then(res => {
                SetCustomerInfo(res.data.result);
                setEditMode(true);
          }).catch(); };

    const AddUpdateCustomer = () =>
    {
        let newObj = Object.assign({}, CustomerInfo);
        CustomerSchema().validate(CustomerInfo, { abortEarly: false })
        // eslint-disable-next-line no-unused-vars
        .then(function (_e) {
            // alert('perfect');
            if(newObj.customerId > 0){
                CustomerAPI.UpdateCustomer(newObj).then(res=>{
                    LoadCustomer(res.data.result.customerId);
                    swal.fire({
                        title: 'Customer update  successfully.',
                        didClose: (()=>{ navigate('/Customerlist');}) ,
                        ...DefaultConfig.SwalGlobal
                    });
                }).catch((err)=>{
                    let ee = err.response.data.errors[Object.keys(err.response.data.errors)[0]][0];
                    swal.fire({ title: ee, ...DefaultConfig.SwalGlobal});
                });
            }else{
                CustomerAPI.AddCustomer(newObj).then(res=>{
                    LoadCustomer(res.data.result.customerId);
                    swal.fire({
                        title: 'Customer add successfully.',
                        didClose: (()=>{ navigate('/Customerlist');}) ,
                        ...DefaultConfig.SwalGlobal
                    });
                }).catch((err)=>{
                    let ee = err.response.data.errors[Object.keys(err.response.data.errors)[0]][0];
                    swal.fire({ title: ee, ...DefaultConfig.SwalGlobal});
                });
            }
            
            // eslint-disable-next-line no-unused-vars
        }).catch((err) => {
            // eslint-disable-next-line no-unused-vars
            swal.fire({ title: err.inner[0].message, ...DefaultConfig.SwalGlobal});
        });
    };
    

    useEffect(() => {
        if(QueryParams.cid === undefined)
        {
            SetCustomerInfo(Customerinit);
        } else {
            LoadCustomer(QueryParams.cid);
        }
     }, [QueryParams.cid]);

     useEffect(() => {
        if(QueryParams.cid !== undefined)
        {
            LoadCustomer(QueryParams.cid);
        }
     }, []);

     useEffect(() => {
            SetCustomerInfo(Customerinit);
     }, []);

     const addNewCustomer = () =>
     {
        SetCustomerInfo(Customerinit);
        setValue(0);
        navigate('/Customer');
     };

    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }

    const handleChange = (name, value) => {
        SetCustomerInfo({ ...CustomerInfo, [name]: value });
    };

    const ChangecustomerShip = (name, value) =>{
        SetCustomerInfo({ ...CustomerInfo,customerShipTo:{ ...CustomerInfo.customerShipTo, [name]: value }});
    };

    return (
        <>
            <div id='pos_view'>
                <div className='depart-main-view'>
                    <div className='top-filter-row'>
                        <div className='lt-title-bx'>
                            <h2>{EditMode ? 'Edit' : 'Add' } Customer Maintenance</h2>
                        </div>
                        <div className='rt-filter-bx filter-hd'>
                            <div className='row searchbar-mainview'>
                                <div className='icons-list'>
                                    {/* <button className='icons-hd-cm plus-icons' onClick={()=>{
                                        addNewCustomer();
                                    }}></button> */}
                                    <button className='icons-hd-cm data-list-icons' onClick={() => { navigate('/Customerlist'); }}></button>
                                    <button className='icons-hd-cm checks-icons' onClick={() => { 
                                        AddUpdateCustomer();
                                    }}></button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <hr className='full-width' />
                    <div className='margin-depart table-cm customer-maintenance-page'>
                        <div className='row'>
                            <div className='col-md-12 standard-left'>
                                <div className='row min-row'>

                                <div className='form-group col-md-4'>
                                        <div className='row'>
                                            <label htmlFor='Cost' className='col-sm-4 col-form-label'>Customer Code<span>*</span></label>
                                            <div className='col-md-8'>
                                                <input type='text'
                                                    disabled={EditMode}
                                                    value={CustomerInfo.customerCode}
                                                    name='customerCode'
                                                    placeholder='Customer Code'
                                                    onChange={(e) => { handleChange(e.target.name, e.target.value); }}
                                                    className='form-control' id='customerCode' />
                                            </div>
                                        </div>
                                    </div>

                                    <div className='form-group col-md-4'>
                                        <div className='row'>
                                            <label htmlFor='Cost' className='col-sm-4 col-form-label'>First Name<span>*</span></label>
                                            <div className='col-md-8'>
                                                <input type='text'
                                                    value={CustomerInfo.firstName}
                                                    name='firstName'
                                                    placeholder='First Name'
                                                    onChange={(e) => { handleChange(e.target.name, e.target.value); }}
                                                    className='form-control' id='Cost' />
                                            </div>
                                        </div>
                                    </div>

                                    <div className='form-group col-md-4'>
                                        <div className='row'>
                                            <label htmlFor='ProductName' className='col-sm-4 col-form-label'>Last Name<span>*</span></label>
                                            <div className='col-md-8'>
                                                <input type='text'
                                                    value={CustomerInfo.lastName}
                                                    onChange={(e) => { handleChange(e.target.name, e.target.value); }}
                                                    name='lastName'
                                                    placeholder='Last Name'
                                                    className='form-control' id='ProductName' />
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                        <Box className='tabs-cm' sx={{ width: '100%' }}>
                            <Box>
                                <Tabs value={value} onChange={handletabChange} aria-label='basic tabs example'>
                                    <Tab label='General Info' {...a11yProps(0)} />
                                    <Tab label='Account Info' {...a11yProps(1)} />
                                    <Tab label='Shipping/Billing' {...a11yProps(2)} />
                                    <Tab label='History' {...a11yProps(3)} />
                                    <Tab label='Note' {...a11yProps(4)} />
                                </Tabs>
                            </Box>
                            <Box className='tabs-panel'>
                                {value === 0 && (<GeneralInfo Data={CustomerInfo} handleChange={handleChange} />)}
                                {value === 1 && (<AccountInfo Data={CustomerInfo} handleChange={handleChange} />)}
                                {value === 2 && (<ShippingAndBilling Data={CustomerInfo} handleChange={ChangecustomerShip} />)}
                                {value === 3 && (<History Data={CustomerInfo} handleChange={handleChange} />)}
                                {value === 4 && (<>
                                    <div className="table-cm note-page">
                                        <div className="row">
                                            <div className="form-group col-md-12">
                                                <div className="row">
                                                    <label className="col-sm-12 col-form-label">You can enter notes to describe your item. This can be used to describe instructions to care for a plant, ingredients inside a meal, or other useful information.</label>
                                                    <div className="col-md-12"><textarea type="text" 
                                                     value={CustomerInfo.note}
                                                     onChange={(e) => { handleChange(e.target.name, e.target.value); }}
                                                     name='note'
                                                     className="form-control" placeholder=""></textarea>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </>)}
                            </Box>
                        </Box>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Customer;



