/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, { useState, useEffect } from 'react';
import Select from 'react-select';
import TextField from '@mui/material/TextField';
import { CusDateFormater } from 'Helpers/StringHelpers';
import { TagsInput } from 'Components/Utility';
import CurrencyInput from 'react-currency-input-field';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { MuiSpinner, ReactSpinner } from 'Components/Utility';
import { AiOutlineClose } from 'react-icons/ai';
import { Invoices, Payments } from './AccountDetailsPart';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';


const AccountInfo = (props) => {
    const [AccountDetailsModel, setAccountDetailsModel] = useState(false);
    const handleClose = () => setAccountDetailsModel(false);
    const [value, setValue] = useState(0);
    const handletabChange = (event, newValue) => {
        setValue(newValue);
    };
    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }

    return (
        <>
            <div className="price-levels-top">
                <div className="icons-list price-levels-btn-right">
                    <button className="levels-plus-icons icons-hd-cm" onClick={(e) => setAccountDetailsModel(true)}>Detailed Account Info</button>
                </div>
            </div>
            <div className="row">
                <div className="col-md-6">
                    <div className='row'>
                        <div className="col-md-2 form-check-lf">
                            <div className="form-check">
                                <label className="form-check-label">Standard</label>
                                <input
                                    className="form-check-input"
                                    value="1"
                                    checked={props.Data.accountType === 1}
                                    name="accountType"
                                    onChange={(e) => {
                                        props.handleChange(e.target.name, 1);
                                    }}
                                    type="radio"
                                ></input>
                            </div>
                        </div>
                        <div className="col-md-1 form-check-lf"></div>
                        <div className="col-md-2 form-check-lf">
                            <div className="form-check">
                                <label className="form-check-label">Layaway</label>
                                <input
                                    className="form-check-input"
                                    value="2"
                                    checked={props.Data.accountType === 2}
                                    name="accountType"
                                    onChange={(e) => {
                                        props.handleChange(e.target.name, 2);
                                    }}
                                    type="radio"
                                ></input>
                            </div>
                        </div>
                        <div className="col-md-2">
                            <div className="info-bx">
                                <div className="form-check">
                                    <label className="form-check-label">Requrie PO # Entry</label>
                                    <input className="form-check-input"
                                        onChange={(e) => {
                                            props.handleChange('isPOEntryRequired', e.target.checked);
                                        }}
                                        name='isPOEntryRequired'
                                        checked={props.Data.isPOEntryRequired}
                                        type="checkbox"></input>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className='row'>
                        <div className="col-md-8">
                            <div className="form-group row tab-group">
                                <label className="col-sm-5 col-form-label">Open Account Date</label>
                                <div className="col-md-6">


                                    <TextField
                                        className='data-picker-field Expiration-Coupon'
                                        value={props.Data.openAccountDate !== null ? CusDateFormater(props.Data.openAccountDate) : props.Data.openAccountDate}
                                        id="datetime-local"
                                        type="date"
                                        name="openAccountDate"
                                        sx={{
                                            '& legend': { display: 'none' },
                                            '& fieldset': { top: 0 },
                                        }}
                                        onChange={(e) => {
                                            props.handleChange(e.target.name, e.target.value);
                                        }}
                                    />

                                </div>
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="form-group row tab-group">
                                <label className="col-sm-5 col-form-label">Close Account Date</label>
                                <div className="col-md-6">

                                    <TextField
                                        className='data-picker-field Expiration-Coupon'
                                        value={props.Data.closeAccountDate !== null ? CusDateFormater(props.Data.closeAccountDate) : props.Data.closeAccountDate}
                                        id="datetime-local"
                                        type="date"
                                        name="closeAccountDate"
                                        sx={{
                                            '& legend': { display: 'none' },
                                            '& fieldset': { top: 0 },
                                        }}
                                        onChange={(e) => {
                                            props.handleChange(e.target.name, e.target.value);
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>



                </div>
                <div className="col-md-6">
                    <div className='row'>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Max Balance</label>
                                <div className="col-md-8">
                                    <CurrencyInput className='form-control'
                                        prefix='$'
                                        name="creditLimit"
                                        id="creditLimit"
                                        value={props.Data.creditLimit}
                                        data-number-to-fixed="5"
                                        data-number-stepfactor="100"
                                        placeholder=""
                                        allowDecimals
                                        decimalsLimit="5"
                                        onValueChange={(e) => {
                                            props.handleChange('creditLimit', e);
                                        }}
                                        disableAbbreviations
                                    />

                                </div>
                            </div>
                        </div>

                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-5 col-form-label">Restrict spending to</label>
                                <div className="col-md-7">

                                    <CurrencyInput className='form-control'
                                        prefix='$'
                                        name="restrictSpendingTo"
                                        id="restrictSpendingTo"
                                        value={props.Data.restrictSpendingTo}
                                        data-number-to-fixed="5"
                                        data-number-stepfactor="100"
                                        placeholder=""
                                        allowDecimals
                                        decimalsLimit="5"
                                        onValueChange={(e) => {
                                            props.handleChange('restrictSpendingTo', e);
                                        }}
                                        disableAbbreviations
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Over</label>
                                <div className="col-md-8">

                                    <CurrencyInput
                                        className='form-control'
                                        name="bonusPointAchived"
                                        id="qty"
                                        data-number-to-fixed="5"
                                        data-number-stepfactor="100"
                                        value={props.Data.over}
                                        placeholder=""
                                        onValueChange={(e) => {
                                            props.handleChange('over', e);
                                        }}
                                        allowDecimals
                                        decimalsLimit="5"
                                        disableAbbreviations
                                    //disabled={MixAndMatchData.priceGroupTypeId !== 1}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-5 col-form-label">Days Starting</label>
                                <div className="col-md-7">
                                    <TextField
                                        className='data-picker-field Expiration-Coupon'
                                        value={props.Data.daysStarting !== null ? CusDateFormater(props.Data.daysStarting) : props.Data.daysStarting}
                                        id="datetime-local"
                                        type="date"
                                        name="daysStarting"
                                        sx={{
                                            '& legend': { display: 'none' },
                                            '& fieldset': { top: 0 },
                                        }}
                                        onChange={(e) => {
                                            props.handleChange(e.target.name, e.target.value);
                                        }}
                                    />

                                </div>
                            </div>
                        </div>


                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-4 col-form-label">Attn:</label>
                                <div className="col-md-8">
                                    <input type="text"
                                        name="attn"
                                        value={props.Data.attn}
                                        defaultValue={props.Data.attn}
                                        onChange={(e) => { props.handleChange(e.target.name, e.target.value); }}
                                        className="form-control" placeholder=""></input>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group row tab-group">
                                <label className="col-sm-5 col-form-label">Balance Due</label>
                                <div className="col-md-7">

                                    <CurrencyInput className='form-control'
                                        prefix='$'
                                        name="balanceDue"
                                        id="balanceDue"
                                        //value={CouponData.product.price}
                                        value={props.Data.balanceDue}
                                        data-number-to-fixed="5"
                                        data-number-stepfactor="100"
                                        placeholder=""
                                        allowDecimals
                                        decimalsLimit="5"
                                        onValueChange={(e) => {
                                            props.handleChange('balanceDue', e);
                                        }}
                                        disableAbbreviations
                                    />

                                </div>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group row tab-group">
                                <label className="col-sm-3 col-form-label">Authorized Members</label>
                                <div className="col-md-8 card-swipe-bx">
                                    <TagsInput selectedTags={(e) => { props.handleChange('authorizedMembers', e); }}
                                        tags={props.Data.authorizedMembers} />
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>

            <Modal show={AccountDetailsModel} className="table-cm cm-popup modal-lg account-details-popup">
                <Modal.Header>
                    <Modal.Title><span>Accoutn Details</span><Button variant="closepopup" onClick={handleClose}><AiOutlineClose style={{ fontSize: '1.3020833333333333vw' }} /></Button></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Box className='tabs-cm mt-0' sx={{ width: '100%' }}>
                        <Box>
                            <Tabs value={value} onChange={handletabChange} aria-label='basic tabs example'>
                                <Tab label='Invoices' {...a11yProps(0)} />
                                <Tab label='Payments' {...a11yProps(1)} />
                            </Tabs>
                        </Box>
                        <Box className='tabs-panel'>
                        {value === 0 && (<Invoices />)}
                        {value === 1 && (<Payments />)}
                              
                        </Box>
                    </Box>
                      <div className="mt-2">
                                            <div className="row">
                                                 <div className="col-md-6 customer-in-row">
                                                      <div className="customer-dts">
                                                           <span><b>Customer Id:</b>115</span>
                                                           <span><b>Customer Name:</b>Kadam shah</span>
                                                      </div>
                                                      <div className="balance-due">
                                                           <span><b>Balance Due:</b>$0.00</span>
                                                      </div>
                                                 </div>
                                                  <div className="col-md-6 jobcode">
                                                  <div className="btn-right">
                                                              <button type="button" className="save-btn btn btn-save colorblue">Apply Payment</button>
                                                              <button type="button" className="save-btn btn btn-save colorblue">Statement</button>
                                                              <button type="button" className="save-btn btn btn-save colorblue">Clear</button>
                                                              <button type="button" className="save-btn btn btn-save colorblue">Open Invoice</button>
                                                 </div>

                                                 </div>
                                            </div>
                                       </div>

                                                                              <div className="mt-3">
                                            <div className="row">
                                                  <div className='col-md-6'>
                                                        <div className='form-group row tab-group'>
                                                            <label htmlFor='Tax' className='col-sm-4 col-form-label'>Select Transaction Type</label>
                                                            <div className='col-md-8'>
                                                                    <Select options={[]} className='product-depatment' />
                                                            </div>

                                                         </div>
                                                  </div>

                                                  <div className='col-md-6'>
                                                        <div className='form-group row tab-group'>
                                                            <label htmlFor='Tax' className='col-sm-4 col-form-label'>Select Inv#</label>
                                                            <div className='col-md-8'>
                                                                    <Select options={[]} className='product-depatment' />
                                                            </div>

                                                         </div>
                                                  </div>

                                                    <div className='col-md-6'>
                                                        <div className='form-group row tab-group'>
                                                            <label htmlFor='Tax' className='col-sm-4 col-form-label'>Pay By</label>
                                                            <div className='col-md-8'>
                                                                    <Select options={[]} className='product-depatment' />
                                                            </div>

                                                         </div>
                                                  </div>

                                                   <div className='col-md-6'>
                                                        <div className='form-group row tab-group'>
                                                            <label htmlFor='Tax' className='col-sm-4 col-form-label'>Amount</label>
                                                            <div className='col-md-8'>
                                                                   <input type="text" name="Default Wage" className="form-control" placeholder="$0.00"></input>
                                                            </div>

                                                         </div>
                                                  </div>

                                            </div>
                                       </div>
                </Modal.Body>
            </Modal>
        </>
    );
};

export default AccountInfo;